.headerStyle{
  justify-content: flex-end;
  background-color: #292D4E!important;
  height:60px!important;
  line-height: 60px !important;
  padding:0 2.36%;
  color:#FFFFFF;
  font-size: 16px;
  text-align: left;
  .title{
    width:229px;
    height:100%;
    float: left;
    line-height: 60px;
    text-align:left;
    cursor: pointer;
    img{
      margin-right:2.6%;
    }
    span{
      font-size:14px;
      font-family: SourceHanSansSC-regular;

    }
  }
   .headerEnd{
     height:100%;
     display: flex;
     justify-content: flex-end;
     .user{
       white-space: nowrap;
       float:right;
       height:100%;
       text-align: right;
       cursor: pointer;
       span{
         height: 100%;
       }
       .name{
         padding:0 3.5% 0 4.5%;
       }
       :global(.anticon-caret-down){
         transition: all 0.2s ease-in-out;
         -webkit-transition: all 0.2s ease-in-out;
         -moz-transition: all 0.2s ease-in-out;
         -o-transition: all 0.2s ease-in-out
       }
       .up{
         transform: rotate(180deg);
         -webkit-transform: rotate(180deg);
         -moz-transform: rotate(180deg);
         -o-transform: rotate(180deg);
         -ms-transform: rotate(180deg);
       }
     }
     .navTitle{
       margin-right:111px;
       background-color: #292D4E;
       :global(.ant-menu-item ){
         width:120px;
         text-align: center;
         span{
           display: inline-block;
           color:#FFFFFF;
           font-size: 16px;
           font-family: SourceHanSansSC-regular;
         }
       }
       :global(.ant-menu-item-selected ){
         background-color: #6597FF !important;
       }
     }
   }
}
:global(.ant-popover-inner-content){
  padding:9px 0!important;
  width:112px;
  text-align: center;
  div{
  cursor: pointer;
    color:#101010;
  }

}